// 引入路由类和 Vue 类
import Router from 'vue-router'
import Vue from 'vue'
import Home from '../components/Home'

// 注册路由类
Vue.use(Router);

// 初始化路由类对象

export default new Router({
  // 设置路由模式为'history'
  mode: 'history',
  routes: [
    //  路由列表
    // {  一个字典，代表一条url
    //    name: "路由别名",
    //    path: "路由地址",
    //    component: 组件类名,
    // }
    {
      name: 'Home',
      path: '/',
      component: Home
    }
  ]
})



